/**
 * Vrifie si le nom ou prnom est valide.
 * 
 * @param {string} name - Le nom ou prnom  valider.
 * @returns {boolean} Retourne true si le nom ou prnom est valide, sinon false.
 */
function validateName(name) {
  return /^[a-zA-Z-\s'-]+$/.test(name.trim());
}

/**
 * Vrifie si l'adresse e-mail est valide.
 * 
 * @param {string} email - L'adresse e-mail  valider.
 * @returns {boolean} Retourne true si l'adresse e-mail est valide, sinon false.
 */
function validateEmail(email) {
  return /\S+@\S+\.\S+/.test(email.trim());
}

/**
 * Vrifie si la ville est valide.
 * 
 * @param {string} city - La ville  valider.
 * @returns {boolean} Retourne true si la ville est valide, sinon false.
 */
function validateCity(city) {
  return /^[a-zA-Z-\s'-]+$/.test(city.trim());
}

/**
 * Vrifie si le code postal est valide.
 * 
 * @param {string} postalCode - Le code postal  valider.
 * @returns {boolean} Retourne true si le code postal est valide, sinon false.
 */
function validatePostalCode(postalCode) {
  return /^\d{5}$/.test(postalCode.trim()) && /^\d+$/.test(postalCode.trim());
}

/**
 * Vrifie si le mot de passe est valide.
 * 
 * @param {string} password - Le mot de passe  valider.
 * @returns {boolean} Retourne true si le mot de passe est valide, sinon false.
 */
function validatePassword(password) {
  const passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
  return passwordPattern.test(password);
}

/**
 * Vrifie si la date de naissance est valide.
 * 
 * @param {Date} date - La date de naissance  valider.
 * @returns {boolean} Retourne true si la date de naissance est valide, sinon false.
 */
function validateDate(date) {
  const today = new Date();
  const minAgeDate = new Date(today.getFullYear() - 18, today.getMonth(), today.getDate());
  if(date <= minAgeDate) {
    return true;
  } else {
    return false;
  }
}

module.exports = {
  validateName,
  validateEmail,
  validateCity,
  validatePostalCode,
  validatePassword,
  validateDate
}